import React, { useCallback, useState } from 'react';
import './index.css'
import { useTranslation } from 'react-i18next'
import { SecondaryLittleButton } from '../Button'
import styled from 'styled-components';
import { supportedWalletTypes, loadAccount } from '../../utils/AccountUtils'
import WalletSelectDialog from './walletSelectDialog'
import _ from 'lodash'
import { useAccountInfoUpdate } from '../../state/wallet/hooks'
import { useApiInited, useApiConnected } from '../../state/api/hooks'
import { useWrongNetworkUpdate, useWrongNetwork } from '../../state/wallet/hooks'
import { WalletType } from '../../utils/AccountUtils'
import { BlackButton } from "../../components/Button/index";

export const Wrapper = styled.div`
  width: 100%;
`

const LittleConnectButton = styled(SecondaryLittleButton)`
  padding: 6px 10px;
  line-height: 16px;

  &:disabled {
    color: #F99E3C;
    border: 1px solid #F99E3C;
    background-color: transparent;
  }
`

interface WalletConnectCompProps {
  btnStyle?: string,
  onButtonClick?: () => void,
  onWalletClose?: (value?: WalletType) => void
}
export default function WalletConnectComp({
  btnStyle = 'bottom',
  onButtonClick,
  onWalletClose
}: WalletConnectCompProps): React.ReactElement {
  const [isOpen, setOpen] = useState(false)

  const { t } = useTranslation()

  const updateAccountInfo = useAccountInfoUpdate()

  const apiInited = useApiInited()
  const apiConnected = useApiConnected()

  const updateWrongNetwork = useWrongNetworkUpdate()
  const wrongNetwork = useWrongNetwork()

  const handleClick = () => {
    setOpen(true)
    onButtonClick && onButtonClick()
  }

  const handleWalletClose = useCallback((value?: WalletType) => {
    setOpen(false)
    if (_.isEmpty(value)) {
      return
    }

    async function getAcount() {
      if (!apiInited) {
        // toast('Api is not ready, please try later!')
        return
      }
      const msg = await loadAccount(value, updateAccountInfo, updateWrongNetwork);
      if (msg !== 'ok') {
        // toast(t(msg))
      }
    }

    getAcount()
    onWalletClose && onWalletClose(value)
  }, [apiInited, onWalletClose, updateAccountInfo, updateWrongNetwork]);

  return (
    <Wrapper>
      {
        btnStyle === 'bottom' && <BlackButton onClick={handleClick} disabled={!apiInited || !apiConnected || wrongNetwork}>{t('connectToWallet')}</BlackButton>
      }
      {
        btnStyle === 'top' && <BlackButton onClick={handleClick} disabled={!apiInited}>{apiInited ? t('connectToWallet') : t('connecting')}</BlackButton>
      }

      <WalletSelectDialog
            walletTypes={supportedWalletTypes}
            open={isOpen}
            onClose={handleWalletClose}></WalletSelectDialog>
    </Wrapper>
  )

}
