import React, { useState } from 'react';
import styled from 'styled-components';
import Column, {ColumnCenter} from '../../components/Column'
import {RowBetween} from '../../components/Row'
import Modal from '../../components/Modal'
import CloseIcon from '../../assets/images/close_icon.svg'
import CloverIcon from '../../assets/images/clover_icon.svg'
import { BlackButton } from "../../components/Button/index";
import { Text } from 'rebass';
import { shortenAddress } from '../../utils'
import InputBase from '@material-ui/core/InputBase'
import StakeStatusModal from './stakeStatus'
import BigNum, {minus, toFixedWithTrim}  from '../../types/bigNum';
import { INPUT_NUMBER_REGEX } from '../../constants'

const customStyle = "position: relative;overflow: visible; max-width:472px; border-radius: 0;"

const Head = styled(RowBetween)`
  width: 100%;
  padding: 40px 0 20px;
  position: relative;
  text-align: center;
`
const Title = styled.div`
  width: 100%;
  font-size: 26px;
  color: #000;
  text-align: center;
  font-family: Optima;
  font-weight: bold;
`

const CloseButton = styled.div`
  cursor: pointer;
  position: absolute;
  right: 24px;
  top: 24px;
`
const BodyWrapper = styled(Column)`
  width: 100%
`

const Wrapper = styled(ColumnCenter)`
  padding: 0 40px 40px;
  overflow: auto;
  display: flex;
`

const Remind = styled.div`
        width: 100%;
  height: 170px;
  padding: 20px 24px;
  color: #fd531d;
  font-size: 14px;
  line-height: 17px;
  font-family: Helvetica Neue;
  background-color: #fff6f4;
  border-top: 4px solid #FD531D;
`

const InfoItem = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 24px;
  width: 100%;
`

const Max = styled.div`
  font-size: 14px !important;
  padding: 4px;
  height: 20px;
  line-height: 10px;
  border: 1px solid #000;
`

const Footer = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-top: 40px;
`

const Understand = styled.div`
  display: flex;
  align-items: center;
`

interface StakeDotProps {
    isOpen: boolean
    onClose: (state: string) => void
    stakeType?: string
    fund: any
    signAndStake: (amount: string, paraId: number) => void
    accountInfo: any
    minContribute: string
}

export default function StakeDotModal({isOpen, onClose, stakeType, fund, signAndStake, accountInfo, minContribute}: StakeDotProps): React.ReactElement {
    const [amount, setAmount] = useState('')
    const [understand, setUnderstand] = useState(false)
    const [errorMsg, setErrorMsg] = useState('')

    const handleClick = () => {
        onClose('confirm')
    }

    const handleChange = (amount: string) => {
        if (amount === '' || INPUT_NUMBER_REGEX.test(amount)) {
            setAmount(amount)
        }
        
    }
    const handleStake = (amount: string, balanceReal: string) => {
        if (accountInfo.address === '') {
            setErrorMsg('Please connect wallet first!')
            return;
        }

        if (fund.info.status !== 'ongoing') {
            setErrorMsg('The crowdloan has ended!')
            return
        }
        if (amount === undefined || amount === '') {
            setErrorMsg('Invalid Amount!')
            return
        }

        const amountBn = BigNum.fromRealNum(amount)
        const balanceBn = BigNum.fromRealNum(balanceReal)
        const balanceMaxStr = minus(BigNum.fromRealNum(balanceReal).bigNum, '1')
        const balanceMaxBn = BigNum.fromBigNum(balanceMaxStr)

        if (amountBn.gt(balanceMaxBn)) {
            setErrorMsg('Insufficient balance!')
            return
        }

        if (amountBn.lt(BigNum.fromBigNum(minContribute))) {
            setErrorMsg('Less than minimum allowed contribution!')
            return
        }

        signAndStake(amount, fund.paraId)
    }
    const title = stakeType === 'Stake KSM' ? 'Stake KSM' : 'Stake More KSM'
    const balanceReal = accountInfo.address !== "" ? BigNum.fromBigNum(accountInfo.tokenAmounts[0].amount).realNum : '-'
    const balanceMaxStr = balanceReal === '-' ? '' : minus(BigNum.fromRealNum(balanceReal).bigNum, BigNum.fromRealNum('1').bigNum)
    const balanceMaxReal = balanceMaxStr === '' ? '' : BigNum.fromBigNum(balanceMaxStr).realNum

    return (
        <Modal isOpen={isOpen} onDismiss={handleClick} maxHeight={90} customStyle={customStyle}>
            <BodyWrapper>
                <Head>
                    <Title>{title}</Title>
                    <CloseButton onClick={() => onClose('close')}>
                        <img width={20} height={20} src={CloseIcon} alt=""/>
                    </CloseButton>
                </Head>
                <Wrapper>
                    <Remind>Bonded amount will be locked for the whole duration of the lease. Staking rule details...</Remind>
                    <InfoItem>
                        <Text fontSize="11px" color="rgba(0, 0, 0, 0.3)">Staked to</Text>
                        <Text fontSize="14px" fontWeight="bold"><img width={30} height={30} src={fund.info.projectLogo} alt=""/>{fund.info.projectName ? fund.info.projectName : fund.paraId}</Text>
                    </InfoItem>
                    {/* <InfoItem>
                        <Text fontSize="11px" color="rgba(0, 0, 0, 0.3)">Contract Address</Text>
                        <Text fontSize="14px" fontWeight="bold" className='text-right'>
                            {shortenAddress('5D29365F05D29365F05D29365F02P0D5A')}
                            <Text fontSize="11px" fontWeight="bold" color="rgba(0, 0, 0, 0.3)">View on CloverScan</Text>
                        </Text>
                    </InfoItem> */}
                    {stakeType === 'Stake KSM' && (
                        <InfoItem>
                            <Text fontSize="11px" color="rgba(0, 0, 0, 0.3)">Staking period</Text>
                            <Text fontSize="14px" fontWeight="bold">{fund.info.lockedTime}</Text>
                        </InfoItem>
                    )}
                    {stakeType !== 'Stake KSM' && (
                        <InfoItem>
                            <Text fontSize="11px" color="rgba(0, 0, 0, 0.3)">My Funds</Text>
                            <Text fontSize="14px" fontWeight="bold">1200.03 DOT</Text>
                        </InfoItem>
                    )}
                    <InfoItem>
                        <Text fontSize="11px" color="rgba(0, 0, 0, 0.3)">Add Staking</Text>
                        <Text fontSize="14px" fontWeight="bold">{`Balance: ${balanceReal} DOT`}</Text>
                    </InfoItem>
                    <InputBase
                        style={{
                            borderBottom: '1px solid #000',
                            marginTop: '6px',
                            fontSize: '32px',
                            fontWeight: 'bold',
                        }}
                        fullWidth
                        value={amount}
                        onChange={(e) => handleChange(e.target.value)}
                        endAdornment={
                            <Max
                                onClick={() => {
                                    setAmount(balanceMaxReal)
                                }}
                            >
                                MAX
                            </Max>
                        }
                        autoFocus={false}
                    />
                    {
                        errorMsg !== "" 
                        && <div style={{color: 'red', fontSize: 14, width: '100%'}}>{errorMsg}</div>
                    }
                    
                    <Footer>
                        <Understand>
                            <input
                                checked={understand}
                                onClick={() => {
                                    setUnderstand(!understand)
                                }}
                                readOnly
                                type="checkbox"
                            />
                            <Text fontSize={14} fontWeight="bold" fontFamily="Inter" marginLeft="8px">I understand</Text>
                        </Understand>
                        <BlackButton width="205px" disabled={!understand} onClick={() => handleStake(amount, balanceReal)}>Sign and Stake</BlackButton>
                    </Footer>
                </Wrapper>
            </BodyWrapper>
        </Modal>
    );
}
