interface StakeStatusProps {
    isOpen: boolean
    onClose: (state: string) => void
    stakeStatus: string
    stakeText: string
    title: string
    btnText: string
    blockHash: string
}

export default function StakeStatusModal({isOpen, onClose, stakeStatus, stakeText, title, btnText, blockHash}: StakeStatusProps): React.ReactElement {
    const handleClick = () => {
        onClose('confirm')
    }

    return (
        <Modal isOpen={isOpen} onDismiss={handleClick} maxHeight={90} customStyle={customStyle}>
            <BodyWrapper>
                <Head>
                    <Title>{title}</Title>
                    <CloseButton onClick={() => onClose('close')}>
                        <img width={20} height={20} src={CloseIcon} alt=""/>
                    </CloseButton>
                </Head>
                <Line/>
                <Wrapper>
                    {(title === 'Stake Successful') && (
                        <NormalContent>
                            <div>
                                <Text fontSize={32} fontFamily="Helvetica Neue" fontWeight="500">{stakeText}</Text>
                                <ScanWrapper>
                                    <Text fontSize={11} fontFamily="Helvetica Neue" color="rgba(0, 0, 0, 0.3)" fontWeight="bold" marginTop="16px">view on</Text>
                                    <Link href={`https://rococo.subscan.io/block/${blockHash}`} target="_blank">CloverScan</Link>
                                </ScanWrapper>
                                
                            </div>
                        </NormalContent>
                    )}
                    {(title === 'Stake Failed') && (
                        <NormalContent>
                            <div>
                                <Text fontSize={32} fontFamily="Helvetica Neue" fontWeight="500">{stakeText}</Text>
                            </div>
                        </NormalContent>
                    )}
                    {(title === 'Stake Start' || title === 'Refund Start' || title === 'Refund Successful' || title === 'Refund Failed') && (
                        <NormalContent>
                            <div>
                                <Text fontSize={32} fontFamily="Helvetica Neue" fontWeight="500">{stakeText}</Text>
                            </div>
                        </NormalContent>
                    )}
                    {title === 'Withdraw Successful' && (
                        <NormalContent>
                            <div>
                                <Text fontSize={32} fontFamily="Helvetica Neue" fontWeight="500">123.34 KSM Withdrawed</Text>
                                <Text fontSize={11} fontFamily="Helvetica Neue" color="rgba(0, 0, 0, 0.3)" fontWeight="bold" marginTop="16px">view on CloseScan</Text>
                            </div>
                            <div>
                                <Text fontSize={32} fontFamily="Helvetica Neue" fontWeight="500" marginTop="26px">123.34 KSM Claimed</Text>
                                <Text fontSize={11} fontFamily="Helvetica Neue" color="rgba(0, 0, 0, 0.3)" fontWeight="bold" marginTop="16px">view on CloseScan</Text>
                            </div>
                        </NormalContent>
                    )}
                    {title === 'Claim Reward' && (
                        <div style={{ width: '100%' }}>
                            <InfoItem>
                                <Text fontSize="11px" color="rgba(0, 0, 0, 0.3)">Unclaimed Reward</Text>
                                <Text fontSize="14px" fontWeight="bold">12.21 CLV</Text>
                            </InfoItem>
                            <InfoItem>
                                <Text fontSize="11px" color="rgba(0, 0, 0, 0.3)">My CLV address</Text>
                                <Text fontSize="14px" fontWeight="bold">{shortenAddress('5ADF8BC35ADF8BC35ADF8BC3F8BC3029')}</Text>
                            </InfoItem>
                        </div>
                    )}
                    {title === 'Withdraw & Claim Reward' && (
                        <div style={{ width: '100%' }}>
                            <InfoItem>
                                <Text fontSize="11px" color="rgba(0, 0, 0, 0.3)">My KSM Staked</Text>
                                <Text fontSize="14px" fontWeight="bold">1234.12 DOT</Text>
                            </InfoItem>
                            <InfoItem>
                                <Text fontSize="11px" color="rgba(0, 0, 0, 0.3)">Fee</Text>
                                <Text fontSize="14px" fontWeight="bold">12.12 DOT</Text>
                            </InfoItem>
                            <InfoItem>
                                <Text fontSize="11px" color="rgba(0, 0, 0, 0.3)">Unclaimed Reward</Text>
                                <Text fontSize="14px" fontWeight="bold">12.21 CLV</Text>
                            </InfoItem>
                            <InfoItem>
                                <Text fontSize="11px" color="rgba(0, 0, 0, 0.3)">My CLV address</Text>
                                <Text fontSize="14px" fontWeight="bold">{shortenAddress('5ADF8BC35ADF8BC35ADF8BC3F8BC3029')}</Text>
                            </InfoItem>
                        </div>
                    )}
                    <BlackButton width="100%" margin="50px 0 0 0" onClick={handleClick}>{btnText}</BlackButton>
                </Wrapper>
            </BodyWrapper>
        </Modal>
    );
}
