export default function ViewDetails(props: any): ReactElement {
    const [openStakeStatusModal, setOpenStakeStatusModal] = useState(false)
    const [modalTitle, setModalTitle] = useState('')
    const [btnText, setBtnText] = useState('')
    const [tableList, setTableList] = useState(Array)

    const funds = useFunds();
    const accountInfo = useAccountInfo();

    
    useEffect(() => {
        const fundsToShow = _.filter(funds, (f) => f.info.uniqueKeys && _.includes(f.info.uniqueKeys, accountInfo.address))
        const tableData = _.map(fundsToShow, (item) => {
            return {
                id: item.paraId,
                project: {
                    projectName: '',
                    subTitle: '',
                },
                amount: '22,238,230 KSM',
                link: '+2.008 USD',
                status: '+56.361 USD',
                roi: {
                    roi: '35.26%',
                    yieldRate: '+28.62%'
                },
                action: {
                    btnText: 'Manage',
                }
            }
        })

        console.log('funds:', funds)
        console.log('funds to show:', fundsToShow)
        console.log('account', accountInfo)

        setTableList(tableData)
    
    }, [accountInfo])
    useEffect(() => {
        setTimeout(() => {
            if (!funds || funds.length === 0) {
                props.history.push('/')
            }
        }, 500)
    }, [funds])
    const onStakeStatusModalClose = () => {
        setOpenStakeStatusModal(false)
    }

    const onStakeStatusModalOpen = (title: string, btnText: string) => {
        setModalTitle(title)
        setBtnText(btnText)
        setOpenStakeStatusModal(true)
    }

    const pathName = props.location.pathname
    const fund = props.location.state && props.location.state.fund ?  props.location.state.fund : {}

    const actionClick = () => {
        props.history.push('/manage')
    }
    const handleFundsHistory = () => {
        props.history.push('/fundsHistory')
    }
    const getContent = () => {
        switch (pathName) {
            case '/viewDetails':
                return <ViewDetail actionClick={actionClick} fund={tableList}></ViewDetail>
            case '/manage':
                return (
                    <ManageView handleFundsHistory={handleFundsHistory} onStakeStatusModalOpen={onStakeStatusModalOpen}></ManageView>
                )
            case '/fundsHistory':
                return (
                    <FundsHistoryView></FundsHistoryView>
                )
        }
    }

    return (
        <ViewDetailsWrapper>
            <ContentWrapper>
                <BackWrapper>
                    <BackButton onClick={() => props.history.goBack()}>
                        <img height={12} width={8} src={BackArrow} alt=""/>
                    </BackButton>
                    {pathName === '/manage' && <Text fontSize={14} color="rgba(0, 0, 0, 0.4)" marginRight="58px" marginTop="14px" fontFamily="IBM Plex Mono">View in CloverScan</Text>}
                </BackWrapper>
                {getContent()}
            </ContentWrapper>
            {openStakeStatusModal &&
                <StakeStatusModal
                    isOpen={openStakeStatusModal}
                    onClose={onStakeStatusModalClose}
                    stakeStatus="success"
                    stakeText=""
                    blockHash=""
                    title={modalTitle}
                    btnText={btnText}
                />
            }
        </ViewDetailsWrapper>
    )
}

