import React, { useCallback, useState } from 'react';
import QuestionIcon from "../../assets/images/question_icon.svg";
import { withStyles } from "@material-ui/core/styles";
import Tooltip from '@material-ui/core/Tooltip';
import {useTranslation} from "react-i18next";
import {Placement} from "@popperjs/core";

const TextOnlyTooltip = withStyles({
    tooltip: {
        color: "rgb(0,0,0)",
        backgroundColor: "#fff",
        border: "1px solid rgb(0,0,0)",
        boxShadow: "rgb(0 0 0 / 10%) 0 21px 21px -16px",
        padding: "10px",
        textAlign: "left",
        fontSize: "12px",
        lineHeight: "15px",
        borderRadius: "0px",
    },
    img: {
        marginLeft:"8px",
    }

})(Tooltip);

export interface PopoverProps {
    title: string
    placement?: Placement
    children: React.ReactNode
}

export default function NewTooltip({title,children,placement='auto'}:PopoverProps): React.ReactElement{
    return (
        <TextOnlyTooltip title={title}>
            <img className="ml-1" width={12} height={12} src={QuestionIcon} alt=""/>
        </TextOnlyTooltip>
    )
}


