import _ from "lodash";
import {api} from './apiUtils';
import keyring from "@polkadot/ui-keyring";
import { blake2AsU8a, encodeAddress } from '@polkadot/util-crypto';
import { BN_ZERO, u8aConcat, u8aToHex } from '@polkadot/util';
import { stringToU8a } from '@polkadot/util';
import { BigNumber } from "bignumber.js";
import { web3Accounts, web3Enable } from '@polkadot/extension-dapp';

const CROWD_PREFIX = stringToU8a('modlpy/cfund');
const EMPTY_U8A = new Uint8Array(32);

function createAddress (paraId: any): Uint8Array {
    return u8aConcat(CROWD_PREFIX, paraId.toU8a(), EMPTY_U8A).subarray(0, 32);
}

function createChildKey (trieIndex: any): string {
    return u8aToHex(
        u8aConcat(
            ':child_storage:default:',
            blake2AsU8a(
                u8aConcat('crowdloan', trieIndex.toU8a())
            )
        )
    );
}

export const getBestNumber = async () => {
    const rawApi = api.getApi();
    return rawApi.derive.chain.bestNumber().then((bestNumber: any) => {
        return _.toNumber(bestNumber.toString());
    })
}

export const getMinContribution = async () => {
    const rawApi = api.getApi();
    return _.toNumber(rawApi.consts.crowdloan.minContribution.toString())
}

function hasLease (paraId: any, leases: any): boolean {
    return leases.some((l: any) => _.isEqual( l, paraId));
}

function updateFund (bestNumber: any, minContribution: any, data: any, leased: any) {
    data.isCapped = data.info.raised < minContribution;
    data.isEnded = bestNumber > data.info.end;
    data.isWinner = hasLease(data.paraId, leased);
    data.info.raised = (data.info.raised/Math.pow(10, 12)).toFixed(4)

    return data;
}

function getInfo(info: any) {
    const cap = info.cap;
    if (_.startsWith(cap.toString(), '0x')) {
        info.cap = (new BigNumber(cap, 16).toNumber()/Math.pow(10, 12)).toFixed(4)
    } else {
        info.cap = (cap/Math.pow(10, 12)).toFixed(4)
    }
    return info;
}

export const getUseFunds = async () => {
    const rawApi = api.getApi();
    const bestNumber = await getBestNumber();
    const minContribution = await getMinContribution();
    return rawApi.query.crowdloan.funds
        .keys()
        .then((indexes: any) => {
            if (indexes) {
                return indexes.map((item:any) => item.args[0])
            }
        })
        .then((paraIds: any)=> {
            let tasks = Promise.all([
                rawApi.query.crowdloan?.funds.multi(paraIds),
                rawApi.query.slots?.leases.multi(paraIds)
            ]).then(([funds,leases]) => {
                // const leaseArr = _.map(leases, lease => lease.toString())
                let fundArr = _.map(_.filter(_.map(paraIds, (paraId: any, i: any) => [paraId, funds[i].unwrapOr(null)]), (v: any) => !!v[1]),
                    ([paraId, info]) => ({
                    accountId: encodeAddress(createAddress(paraId)),
                    childKey: createChildKey(info.trieIndex),
                    firstSlot: _.toNumber(info.firstSlot.toString()),
                    info: getInfo(JSON.parse(info.toString())),
                    isCrowdloan: true,
                    key: paraId.toString(),
                    lastSlot: _.toNumber(info.lastSlot.toString()),
                    paraId: _.toNumber(paraId.toString()),
                    value: _.toNumber(info.raised.toString())
                }))

                fundArr = _.map(fundArr, (c) => updateFund(bestNumber, minContribution, c, leases))
                    .sort((a, b) =>
                        a.isWinner !== b.isWinner
                            ? a.isWinner
                            ? -1
                            : 1
                            : a.isCapped !== b.isCapped
                            ? a.isCapped
                                ? -1
                                : 1
                            : a.isEnded !== b.isEnded
                                ? a.isEnded
                                    ? 1
                                    : -1
                                : 0
                    )
                return fundArr
            })
            return tasks
        })
        .catch(console.error);
}

export const getUseLeasePeriod = async () => {
    const rawApi = api.getApi();
    const leasePeriod = await rawApi.consts.slots.leasePeriod
    return rawApi.derive.chain.bestNumber().then((bestNumber: any) => {
        const progress = bestNumber.mod(bestNumber);
        return {
            currentPeriod: _.toNumber(bestNumber.div(leasePeriod).toString()),
            bestNumber: _.toNumber(bestNumber.toString()),
            length: leasePeriod,
            progress: progress.toString(),
            remainder: leasePeriod.toString().sub(progress)
        };
    })
}

export const getUseOwnedIds = async () => {
    const rawApi = api.getApi();
    // const allAccounts = await web3Accounts();
    const allAccounts = keyring.getAccounts();

    return await rawApi.query.registrar.paras.entries().then((ownedIds: any) => {
        return ownedIds.map((pair:any) => {
            if (!_.size(pair)) {
                return null;
            }
            const { args } = pair[0]
            const optInfo = pair[1]
            if(optInfo.isNone) return null
            const paraInfo = optInfo.unwrap();
            return {
                manager: paraInfo.manager.toString(),
                paraId: args[0],
                paraInfo
            };
        }).filter((info: any) => !!info && allAccounts.some((a) => a === info.manager))
    })
}

export const getUseAuctionInfo = async () => {
    const rawApi = api.getApi();
    return Promise.all([
        rawApi.query.auctions.auctionCounter(),
        rawApi.query.auctions.auctionInfo()
    ]).then(([counter, info]) => {
        return {
            auctionCounter: counter.toString(),
            auctionInfo: info.toString()
        }
    })
}

interface UseAccounts {
    allAccounts: string[];
    hasAccounts: boolean;
    isAccount: (address?: string | null) => boolean;
}
const EMPTY: UseAccounts = { allAccounts: [], hasAccounts: false, isAccount: () => false };
export const useAccount = (): UseAccounts => {
    const obj = EMPTY
    keyring.accounts.subject.subscribe((accounts) => {
        console.log(accounts)
        obj.allAccounts = accounts ? Object.keys(accounts) : [];
        obj.hasAccounts = obj.allAccounts.length !== 0;
        obj.isAccount = (address?: string | null) => !!address && obj.allAccounts.includes(address);
    })
    // console.log('====================')
    // console.log('====================')
    // console.log(keyring.getAccounts())
    // console.log(obj)
    // console.log('====================')
    // console.log('====================')
    return obj;
}

