import React, {ReactElement, useState, useMemo, Component, useCallback, useEffect} from 'react';
import { BigNumber } from "bignumber.js";
import styled from 'styled-components';
import BN from 'bn.js';
import { Text } from 'rebass'
import Row from '../../components/Row'
import QuestionIcon from '../../assets/images/question_icon.svg'
import CloverIcon from '../../assets/images/clover_icon.svg'
import Table from '../../components/Table'
import StakeDotModal from './stakeDotModal'
import StakeStatusModal from "./stakeStatus";
import NewTooltip from '../../components/NewTooltip'
import {
    loadUseFunds,
    loadUseLeasePeriod,
    loadUseOwnedIds,
    loadUseAuctionInfo
} from '../../utils/auctionUtil'
import {
    useFundsUpdate,
    useFunds,
    useLeasePeriodUpdate,
    useLeasePeriod,
    useOwnedIdsUpdate,
    useOwnedIds,
    useAuctionInfoUpdate,
    useAuctionInfo,
    useAccountInfo
} from '../../state/wallet/hooks'
import {useApiInited} from "../../state/api/hooks";
import _ from "lodash";
import { getMinContribution } from '../../utils/rococoUtils'
import { LINKS } from '../../utils/rococoProjects'
import { emptyLogos, namedLogos } from '../../rococo-config/ui/logos/index';
import { doTransaction } from '../../utils/transUtils';
import BigNum from '../../types/bigNum';

const HomePageWrapper = styled.div`
  width: 100%;
`

const Content = styled.div`
  width: 1280px;
  margin: 0 auto;
  @media (max-width: 767px){
    width: 100%; 
  }
`

const ContentTop = styled(Row)`
    margin: 32px 0 44px 0;
  @media (max-width: 767px){
    display: grid;
  }
`

const ContentTopLeft = styled.div`
  width: 460px;
  height: 400px;
  background-color: #ffffff;
  padding: 44px;
  margin-right: 40px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  @media (max-width: 767px){
    width: 100%;
    margin-bottom: 12px;
  }
`;

const ViewDetails = styled.div`
  width: 100%;
  text-align: center;
  border: 1px solid rgba(0, 0, 0, 0.2);
  padding: 13px 0;
  font-size: 14px;
  font-weight: bold;
  cursor: pointer;
  color: #000;
  
  &:hover {
    border-color: #000;
  }
`;

const CurrencySpan = styled.span`
  font-size: 22px;
  font-font: Optima;
  font-weight: bold;
`

const SuffixSpan = styled.span`
  font-size: 16px;
  font-font: Optima;
  font-weight: bold;
`

const ContentTopRight = styled.div`
  display: flex;
  flex-wrap: wrap;
  width: 780px;
  justify-content: flex-end;
`

const ContentTopRightItem = styled.div`
  width: 382px;
  height: 194px;
  padding: 40px;
  margin-left: 8px;
  background-color: #fff;

  &:first-child, &:nth-child(2) {
    margin-bottom: 12px;
  }
`

export default function HomePage(props: any): ReactElement {
    const [stakeModalOpen, setStakeModalOpen] = useState(false)
    const [openStakeStatusModal, setOpenStakeStatusModal] = useState(false)
    const [selectStakeType, setSelectStakeType] = useState('')
    const [statusTitle, setStatusTitle] = useState('')
    const [statusText1, setStatusText1] = useState('')
    const [selectFund, setSelectFund] = useState({})
    const [minContribute, setMinContribute] = useState('')
    const [blockHash, setBlockHash] = useState('')

    const apiInited = useApiInited()
    const funds = useFunds();
    const leasePeriod = useLeasePeriod();
    const ownedIds = useOwnedIds();
    const AuctionInfo = useAuctionInfo();
    const updateUseFunds = useFundsUpdate()
    const updateUseLeasePeriod = useLeasePeriodUpdate()
    const updateUseOwnedIds = useOwnedIdsUpdate()
    const updateUseAuctionInfo = useAuctionInfoUpdate()
    const accountInfo = useAccountInfo();

    const loadAllData = useCallback(async () => {
        if (!apiInited) {
            return
        }
        await loadUseFunds(updateUseFunds);
        await loadUseLeasePeriod(updateUseLeasePeriod);
        await loadUseOwnedIds(updateUseOwnedIds);
        await loadUseAuctionInfo(updateUseAuctionInfo);
    }, [apiInited, updateUseFunds, updateUseLeasePeriod, updateUseOwnedIds, updateUseAuctionInfo])
    useEffect(() => {
        if (!apiInited) {
            return
        }
        async function getData() {
            await loadUseFunds(updateUseFunds);
            await loadUseLeasePeriod(updateUseLeasePeriod);
            await loadUseOwnedIds(updateUseOwnedIds);
            await loadUseAuctionInfo(updateUseAuctionInfo);
            const mc = await getMinContribution()
            setMinContribute(mc.toString())
        }
        getData()

    }, [apiInited, updateUseFunds, updateUseLeasePeriod, updateUseOwnedIds, updateUseAuctionInfo])
    // console.log('funds info is: ', funds)
    // console.log('leasePeriod is: ', leasePeriod)
    // console.log('ownedIds is: ', ownedIds)
    // console.log('AuctionInfo is: ', AuctionInfo)

    let active: any = null;
    let ended: any = null;
    let allUniqueKeys: any = [];
    const currentPeriod = leasePeriod && leasePeriod.currentPeriod ? leasePeriod.currentPeriod : 0;
    if (funds && currentPeriod) {
        active = _.filter(funds, ({ firstSlot, isCapped, isEnded, isWinner, info }) => !(isCapped || isEnded || isWinner) && !(currentPeriod > firstSlot) && (info.raised !== info.cap));
        ended = _.filter(funds, ({ firstSlot, isCapped, isEnded, isWinner, info }) => (isCapped || isEnded || isWinner) || currentPeriod > firstSlot || (info.raised === info.cap));
        _.map(funds, (fund: any) => { allUniqueKeys =  _.concat(allUniqueKeys, fund.info.uniqueKeys) });
    }
    active = _.orderBy(active, [(a) => (a.info.lastSlot - currentPeriod + 1) * 6, 'paraId'], ['asc', 'asc'])
    ended = _.orderBy(ended, [(e) => (e.info.lastSlot - currentPeriod + 1) * 6, 'paraId'], ['asc', 'asc'])

    const updateFundItem = (item: any, status: string) => {
        item = JSON.parse(JSON.stringify(item))
        const link: any = _.find(LINKS, l => l.paraId === item.paraId)
        const logo = link && link.info ? link.info : null
        const found = logo ? namedLogos[logo] : null;
        const img = (found || emptyLogos.empty) as string
        item.info.projectLogo = img
        item.info.projectName = link && link.text ? link.text : '';
        item.info.status = status;
        item.info.lockedTime = `${(item.info.lastSlot - currentPeriod + 1) * 6} weeks`;
        const blocksLeft = leasePeriod && leasePeriod.bestNumber && item.info.end > leasePeriod.bestNumber
            ? item.info.end + leasePeriod.bestNumber
            : null
        const hasEnded = !blocksLeft && !!leasePeriod && (
            item.isWinner
                ? leasePeriod.currentPeriod > item.info.lastSlot
                : leasePeriod.currentPeriod > item.info.firstSlot
        );
        item.info.blocksLeft = blocksLeft;
        item.info.canContribute = status === 'ongoing' && !item.isCapped && !item.isWinner && !!blocksLeft;
        item.info.canDissolve = _.toNumber(item.info.raised) === 0;
        item.info.canWithdraw = _.toNumber(item.info.raised) !== 0 && hasEnded;
        return item
    }

    const getItemDom = (item: any) => {
        const btnText = item.info.canContribute ? 'Stake KSM' :
            (item.info.canWithdraw && item.info.uniqueKeys && item.info.uniqueKeys.length !== 0 && _.includes(item.info.uniqueKeys, accountInfo.address) ? 'Refund' :
                (item.info.canDissolve ? (item.isEnded ? 'Close' : 'Cancel') : ''))
        const obj: any = {
            id: item.paraId,
            project: {
                projectName: item.info.projectName,
                subTitle: '',
            },
            status: item.info.status,
            totalStaked: (
                <div>

                    {`${item.info.raised} / ${item.info.cap} KSM`}
                </div>
            ),
            bonusToken: 'KSM',
            price: {
                price: '-',
                yieldRate: '-',
            },
            apy: {
                apy: '-',
                subTitle: '-'
            },
            leasePeriod: {
                name: (
                    <div>
                        {`${item.info.firstSlot} - ${item.info.lastSlot}`}
                    </div>
                ),
                period: `KSM locked ${item.info.lockedTime}`
            },
            action: {
                btnText: btnText,
                disabled: btnText === 'Close' || btnText === 'Cancel' || btnText === '',
            },
        }
        return obj
    }
    const tableList: any = []
    active = _.map(active, (act) => {
        act = updateFundItem(act, 'ongoing')
        tableList.push(getItemDom(act))
        return act
    })
    ended = _.map(ended, (end) => {
        end = updateFundItem(end, 'Complete')
        tableList.push(getItemDom(end))
        return end
    })

    const tableHeader = [
        {
            name: 'ID',
            width: '40px',
        },
        {
            name: 'Project',
        },
        {
            name: 'Status',
        },
        {
            name: 'KSM Raised',
        },
        {
            name: 'Bonus Token',
        },
        {
            name: 'Price',
            subTitle: '24h Change',
        },
        {
            name: 'APY',
            subTitle: '30 days avg.',
        },
        {
            name: 'Leases',
            subTitle: 'KSM Locked period',
        },
        {
            name: 'Action',
        },
    ]
    const handleViewDetail = () => {
        // props.history.push('/viewDetails')
        props.history.push({ pathname: '/viewDetails', state: { fund: tableList[0] } })
    }

    const handleStake = (val: any, id: any) => {
        if (val === 'Refund') {
            handleRefund(id)
        } else {
            const fund = _.find(active, f => f.paraId === id)
            setSelectFund(fund)
            setSelectStakeType(val)
            setStakeModalOpen(true)
        }

    }

    const handleRefund = (id: any) => {
        const fund = _.find(ended, f => f.paraId === id)

        const onError = (msg: string) => {
            setStatusTitle('Refund Failed')
            setStatusText1(msg)
        }
        const onStart = () => {
            setStatusTitle('Refund Start')
            setStatusText1('Activity Start....')
        }
        const onEnd = (state: string, blockHash?: string, payload?: any) => {
            if (state === 'complete') {
                setStatusTitle('Refund Successful')
                setStatusText1('Activity Successful')
                setBlockHash(blockHash ?? '')
                loadAllData()
            } else {
                setStatusTitle('Refund Failed')
                setStatusText1(payload ?? 'Activity Rejected')
            }
        }

        doTransaction('refund', [accountInfo.address, fund.paraId], accountInfo.address,
        onError, onStart, onEnd);
        setOpenStakeStatusModal(true)
        onStakeModalClose('confirm')
    }

    const onStakeModalClose = (state: string) => {
        console.log(state)
        setStakeModalOpen(false)
    }

    const signAndStake = (amount: string, paraId: number) => {
        const amountBn = BigNum.fromRealNum(amount).bigNum
        console.log('amount:', amount)
        console.log('amount:', amountBn)
        const onError = (msg: string) => {
        // toast(msg)
        }
        const onStart = () => {
            setStatusTitle('Stake Start')
            setStatusText1('Activity Start....')
        }
        const onEnd = (state: string, blockHash?: string, payload?: any) => {
            if (state === 'complete') {
                setStatusTitle('Stake Successful')
                setStatusText1(`${amount} KSM Staked`)
                setBlockHash(blockHash ?? '')
                loadAllData()
            } else {
                setStatusTitle('Stake Failed')
                setStatusText1('Activity Rejected')
            }
        }

        doTransaction('contribute', [paraId, new BN(amountBn), null], accountInfo.address,
        onError, onStart, onEnd);
        setOpenStakeStatusModal(true)
        onStakeModalClose('confirm')
    }

    const onStakeStatusModalClose = () => {
        setOpenStakeStatusModal(false)
    }
    return (
        <HomePageWrapper>
            <Content>
                {
                    _.includes(allUniqueKeys, accountInfo.address) && <ContentTop>
                        <ContentTopLeft>
                            <Text fontFamily="Optima" fontWeight="bold" fontSize={22}>My Total Staked Value</Text>
                            <Text fontFamily="Optima" fontWeight="bold" fontSize={64}>38.415 <CurrencySpan>KSM</CurrencySpan></Text>
                            <ViewDetails onClick={handleViewDetail}>View Details</ViewDetails>
                        </ContentTopLeft>
                        <ContentTopRight>
                            <ContentTopRightItem>
                                <Text fontSize={16} fontFamily="Optima" fontWeight="bold">My Total's Earnings</Text>
                                <Text fontSize={36} fontFamily="Optima" fontWeight="bold" marginTop="30px">~0.415 <SuffixSpan>USD</SuffixSpan></Text>
                            </ContentTopRightItem>
                            <ContentTopRightItem>
                                <Text fontSize={16} fontFamily="Optima" fontWeight="bold">My Yesterday's Earnings</Text>
                                <Text fontSize={36} fontFamily="Optima" fontWeight="bold" marginTop="30px">~0.415 <SuffixSpan>USD</SuffixSpan></Text>
                            </ContentTopRightItem>
                            <ContentTopRightItem>
                                <Text fontSize={16} fontFamily="Optima" fontWeight="bold" marginRight='8px' className='d-flex align-items-baseline'>
                                    My APY
                                    <NewTooltip title="APR (Annual Percentage Rate): Let's look at a BlockFi loan with a 2% origination fee over a 12-month loan term">
                                    </NewTooltip>
                                </Text>
                                <Text fontSize={36} fontFamily="Optima" fontWeight="bold" marginTop="30px">36.78 <SuffixSpan>%</SuffixSpan></Text>
                            </ContentTopRightItem>
                            <ContentTopRightItem>
                                <Text fontSize={16} fontFamily="Optima" fontWeight="bold" marginRight='8px' className='d-flex align-items-baseline'>My ROI
                                    <NewTooltip title="ROI is calculated by subtracting the initial value of the investment from the final value of the investment (which equals the net return), then dividing this new number (the net return) by the cost of the investment, and, finally, multiplying it by 100.">
                                    </NewTooltip>
                                </Text>
                                <Text fontSize={36} fontFamily="Optima" fontWeight="bold" marginTop="30px">36.78 <SuffixSpan>%</SuffixSpan></Text>
                            </ContentTopRightItem>
                        </ContentTopRight>
                    </ContentTop>
                }
                <div className='homeList' style={{ marginTop: '40px' }}>
                    <Table tableHeader={tableHeader} tableList={tableList} actionClick={handleStake}></Table>
                </div>
            </Content>
            {stakeModalOpen &&
                <StakeDotModal
                    isOpen={stakeModalOpen}
                    onClose={onStakeModalClose}
                    stakeType={selectStakeType}
                    fund={selectFund}
                    signAndStake={signAndStake}
                    accountInfo={accountInfo}
                    minContribute={minContribute}
                />
            }
            {openStakeStatusModal &&
                <StakeStatusModal
                    isOpen={openStakeStatusModal}
                    onClose={onStakeStatusModalClose}
                    stakeStatus="success"
                    stakeText={statusText1}
                    blockHash={blockHash}
                    title={statusTitle}
                    btnText="close"
                />
            }
        </HomePageWrapper>
    )
}

