import React, { useState } from 'react';
import styled from 'styled-components';
import './index.css'
import { useTranslation } from 'react-i18next'
import Modal from '../../components/Modal'
import Column from '../../components/Column'
import { WalletType } from '../../utils/AccountUtils'
import { Text } from 'rebass';
import ArrowRight from "../../assets/images/icon-arrow-right.svg";

const ContentWrapper = styled(Column)`
  width: 100%;
  display: grid;
  overflow: auto;
  justify-content: center;
`;

const Wrapper = styled.div`
  overflow: auto;
`

const CancelButton = styled.div`
  cursor: pointer;
  width: 320px;
  padding: 13px 0;
  border: 1px solid rgba(0, 0, 0, 0.2);
  text-align: center;
  color: #000;
  font-size: 14px;
  font-weight: bold;
  margin-top: 30px;

  &:hover {
    border-color: #000;
  }
`

interface WalletSelectDialogPropTypes {
  onClose: (value?: WalletType) => void,
  open: boolean,
  walletTypes: WalletType[]
}

export default function WalletSelectDialog({ onClose, open, walletTypes }: WalletSelectDialogPropTypes): React.ReactElement {
  const [hoverValue, setHoverValue] = useState({});

  const { t } = useTranslation()
  const handleClose = () => {
    setHoverValue({})
    onClose();
  };

  const handleListItemClick = (value: WalletType) => {
    setHoverValue({})
    onClose(value);
  };

  const customStyle = 'border-radius: 0; width: 420px; padding: 26px 0 47px;'
  return (
    <Modal isOpen={open} onDismiss={handleClose} maxHeight={90} customStyle={customStyle}>
      <ContentWrapper>
        <div className="content-width">
          <div>{t('connectAWallet')}</div>
        </div>
        <Text width="320px" fontSize="13px" fontWeight="normal" lineHeight="20px" marginTop="20px" marginBottom="20px" fontFamily="IBM Plex Mon">{t('connectAWalletInit')}</Text>
        <Wrapper>
          <div className='list-container'>
            {
              walletTypes.map((account: WalletType, index: number) => (
                <div className='item-container' key={index}
                onMouseEnter={() => setHoverValue(account)}
                onMouseLeave={() => setHoverValue({})}
                onClick={() => handleListItemClick(account)}>
                  <div className='dialog-item-text-left'>
                    <img alt={account.icon} src={account.icon}></img>
                    {account.name}
                  </div>
                  <img width={12} src={ArrowRight} alt=""/>
                </div>
              ))
            }
          </div>
          <CancelButton className="wallet-dia-close-btn" onClick={() => handleClose()}>Cancel</CancelButton>
        </Wrapper>
      </ContentWrapper>
    </Modal>
  );
}
