import _ from 'lodash'
import {
    getUseFunds,
    getUseLeasePeriod,
    getUseOwnedIds,
    getUseAuctionInfo
} from './rococoUtils'
import {api} from "./apiUtils";
import { encodeAddress } from '@polkadot/util-crypto';

export async function loadUseFunds(updateUseFunds: (funds: any) => void) {
    const rawApi = api.getApi();
    getUseFunds().then((funds: any) => {
        if (_.isEmpty(funds)) {
            return
        }
        Promise.all(_.map(funds, (fund: any) => rawApi.rpc.childstate.getKeys(fund.childKey, '0x'))).then((arr) => {
            _.map(funds, (f, index: number) => f.info.uniqueKeys = arr[index].map((k: any) => encodeAddress(k)))
            updateUseFunds(funds)
        })
    })
}

export async function loadUseLeasePeriod(updateUseLeasePeriod: (leasePeriod: any) => void) {
    getUseLeasePeriod().then((leasePeriod) => {
        if (_.isEmpty(leasePeriod)) {
            return
        }
        updateUseLeasePeriod(leasePeriod)
    })
}

export async function loadUseOwnedIds(updateUseOwnedIds: (ownedIds: any) => void) {
    getUseOwnedIds().then((ownedIds) => {
        if (_.isEmpty(ownedIds)) {
            return
        }
        updateUseOwnedIds(ownedIds)
    })
}

export async function loadUseAuctionInfo(updateUseAuctionInfo: (auctionInfo: any) => void) {
    getUseAuctionInfo().then((auctionInfo) => {
        if (_.isEmpty(auctionInfo)) {
            return
        }
        updateUseAuctionInfo(auctionInfo)
    })
}
